// src/pages/provider/index.js
import { useState, useEffect } from "react";
import { motion } from "framer-motion";
import { Briefcase, CheckCircle, Clock, XCircle } from "lucide-react";
import { Button } from "@/components/ui/button";
import { Card, CardContent } from "@/components/ui/card";
import toast from "react-hot-toast";

export default function ProviderDashboard() {
  const [activeTab, setActiveTab] = useState("all"); // all | applications
  const [jobs, setJobs] = useState([]);
  const [applications, setApplications] = useState([]);
  const [loading, setLoading] = useState(false);
  const [provider, setProvider] = useState(null);

  useEffect(() => {
    const storedProvider = JSON.parse(localStorage.getItem("provider"));
    if (storedProvider) {
      setProvider(storedProvider);
      fetchJobs();
    } else {
      toast.error("Provider not found ‚Äî please log in again.");
    }
  }, []);

  const fetchJobs = async () => {
    setLoading(true);
    try {
      const res = await fetch(`${process.env.NEXT_PUBLIC_API_URL}/api/jobs/all`);
      const data = await res.json();
      if (data.success) setJobs(data.jobs);
      else toast.error("Failed to load jobs ‚ùå");
    } catch {
      toast.error("Server unreachable ‚ùå");
    } finally {
      setLoading(false);
    }
  };

  const fetchApplications = async () => {
    if (!provider?.id) return;
    setLoading(true);
    try {
      const res = await fetch(
        `${process.env.NEXT_PUBLIC_API_URL}/api/providers/applications/${provider.id}`
      );
      const data = await res.json();
      if (data.success) setApplications(data.applications);
      else toast.error("Failed to load applications ‚ùå");
    } catch {
      toast.error("Server unreachable ‚ùå");
    } finally {
      setLoading(false);
    }
  };

  const handleApply = async (jobId) => {
    if (!provider?.id) {
      toast.error("Login required ‚ùå");
      return;
    }

    try {
      const res = await fetch(
        `${process.env.NEXT_PUBLIC_API_URL}/api/providers/apply`,
        {
          method: "POST",
          headers: { "Content-Type": "application/json" },
          body: JSON.stringify({
            job_id: jobId,
            provider_id: provider.id,
            message: "Hello ‚Äî I can take this job.",
          }),
        }
      );

      const data = await res.json();
      if (data.success) {
        toast.success("Applied successfully ‚úÖ");
        fetchApplications();
      } else {
        toast.error(data.message || "Failed to apply ‚ùå");
      }
    } catch {
      toast.error("Server unreachable ‚ùå");
    }
  };

  const renderJobs = () => (
    <div className="grid md:grid-cols-2 gap-4">
      {jobs.map((job) => (
        <Card
          key={job.id}
          className="bg-white/80 dark:bg-gray-900 shadow-sm border border-gray-200 dark:border-gray-700 hover:shadow-md transition-all"
        >
          <CardContent className="p-4">
            <h3 className="text-lg font-semibold text-gray-900 dark:text-gray-100">
              {job.service}
            </h3>
            <p className="text-gray-600 dark:text-gray-400 text-sm mt-1">
              {job.description}
            </p>
            <div className="flex justify-between items-center mt-3 text-sm">
              <span className="text-gray-500 dark:text-gray-400">
                üìç {job.district}
              </span>
              <span className="font-semibold text-orange-500">${job.budget}</span>
            </div>
            <Button
              className="w-full mt-3 bg-orange-500 hover:bg-orange-600 text-white"
              onClick={() => handleApply(job.id)}
            >
              Apply
            </Button>
          </CardContent>
        </Card>
      ))}
    </div>
  );

  const renderApplications = () => (
    <div className="grid md:grid-cols-2 gap-4">
      {applications.map((app) => (
        <Card
          key={app.id}
          className="bg-white/80 dark:bg-gray-900 border border-gray-200 dark:border-gray-700"
        >
          <CardContent className="p-4">
            <div className="flex justify-between items-start">
              <h3 className="text-lg font-semibold text-gray-900 dark:text-gray-100">
                {app.service}
              </h3>
              <StatusBadge status={app.status} />
            </div>
            <p className="text-gray-600 dark:text-gray-400 text-sm mt-1">
              {app.description}
            </p>
            <div className="flex justify-between items-center mt-3 text-sm">
              <span className="text-gray-500 dark:text-gray-400">
                üìç {app.district}
              </span>
              <span className="font-semibold text-orange-500">${app.budget}</span>
            </div>
            <p className="text-sm text-gray-500 dark:text-gray-400 mt-2 italic">
              {app.message}
            </p>
          </CardContent>
        </Card>
      ))}
      {applications.length === 0 && !loading && (
        <p className="text-center text-gray-500 dark:text-gray-400 mt-10">
          No applications yet üí§
        </p>
      )}
    </div>
  );

  return (
    <motion.div
      className="min-h-screen bg-gray-50 dark:bg-gray-950 text-gray-900 dark:text-white p-6"
      initial={{ opacity: 0 }}
      animate={{ opacity: 1 }}
      transition={{ duration: 0.5 }}
    >
      <h1 className="text-2xl font-bold text-orange-500 mb-6">
        Provider Dashboard
      </h1>

      {/* Tabs */}
      <div className="flex gap-4 mb-6">
        <Button
          variant={activeTab === "all" ? "default" : "outline"}
          onClick={() => {
            setActiveTab("all");
            fetchJobs();
          }}
          className="rounded-xl"
        >
          <Briefcase className="mr-2 w-4 h-4" /> All Jobs
        </Button>
        <Button
          variant={activeTab === "applications" ? "default" : "outline"}
          onClick={() => {
            setActiveTab("applications");
            fetchApplications();
          }}
          className="rounded-xl"
        >
          <Clock className="mr-2 w-4 h-4" /> My Applications
        </Button>
      </div>

      {loading ? (
        <p className="text-center text-gray-500">Loading...</p>
      ) : activeTab === "all" ? (
        renderJobs()
      ) : (
        renderApplications()
      )}
    </motion.div>
  );
}

function StatusBadge({ status }) {
  const colors = {
    pending: "bg-yellow-100 text-yellow-700 dark:bg-yellow-900 dark:text-yellow-300",
    approved: "bg-green-100 text-green-700 dark:bg-green-900 dark:text-green-300",
    rejected: "bg-red-100 text-red-700 dark:bg-red-900 dark:text-red-300",
  };

  const icons = {
    pending: <Clock className="w-4 h-4 mr-1" />,
    approved: <CheckCircle className="w-4 h-4 mr-1" />,
    rejected: <XCircle className="w-4 h-4 mr-1" />,
  };

  return (
    <span
      className={`inline-flex items-center px-2 py-1 rounded-full text-xs font-medium ${colors[status]}`}
    >
      {icons[status]}
      {status.charAt(0).toUpperCase() + status.slice(1)}
    </span>
  );
}
